form{
	padding: 20px 0;
	position: relative;
    z-index: 2;
    justify-items: center;
}	
input, textarea{
	display: block;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
    outline: 0;
    border: none;
	background-color: #1e2125;
	width: 400px;
	border-radius: 3px;
	padding: 10px 15px;
	margin: 0 auto 10px auto;
	display: block;
	text-align: left;
	font-size: 14px;
	color: white;
	font-weight: 300;
}

textarea {
    resize: none;
}

.button-wrapper {
    text-align: center;
}

#add-button{
    margin: 5px;
